import React, {Component} from 'react';
import { connect } from 'react-redux';
import { addToCart } from './actions/cartActions'


class Home extends Component{

    handleClick = (id) => {
       this.props.addToCart(id);
    }

    render(){
        let itemList = this.props.items.map(item=>{
            return(
                <div className='card' key={item.id}>
                   <div className='card-image'>
                       <img src={item.img} alt={item.title}/>
                       <span to="/" onClick={()=>{this.handleClick(item.id)}} className="btn-floating halfway-fab waves-effect waves-light red"><i className="material-icons">add</i></span>
                   </div>

                   <div className='card-content'>
                   <h3 className='card-title'>{item.title}</h3>
                       <p>{item.desc}</p>
                       <p><b>Harga: {item.price} Rp -</b></p>
                   </div>
                </div>
            )
        })
        return(
            <div className='container'>
              <h3 className='center'>Our Shop</h3>
              <div className='box'>
                  {itemList}
              </div>
            </div>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        addToCart: (id) => {
            dispatch(addToCart(id))
        }
    }
}

const mapStateToProps = (state)=>{
    return{
        items: state.items
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Home)