 import Item1 from '../../images/cd1.jpg';
 //import Item2 from '../../images/cd2.jpg';
 import Item3 from '../../images/cd3.jpg';
 import Item4 from '../../images/baju1.jpg';
 import Item5 from '../../images/baju2.jpg' ;
 import Item6 from '../../images/stadia.jpg' ;
 import { ADD_TO_CART, REMOVE_ITEM, SUB_QUANTITY, ADD_QUANTITY, ADD_SHIPPING } from '../actions/action-types/cart-actions'

 const Item2 = require('../../images/cd2.jpeg');

 
 const initState = {
     items: [
        {id: 1, title: 'AC: Valhala', desc: 'Soon holiday 2020', price: 650000, img:Item1},
         {id: 2, title: 'The Witcher 3', desc: 'Game of the year come buy and play it!', price: 300000, img:Item2},
         {id: 3, title: 'Red Redemption 2', desc: 'A game from rockstar studio best game mate!', price: 300000, img:Item3},
         {id: 4, title: 'Baju Garuda', desc: 'Garuda didadaku yuk beli', price: 30.000, img:Item4},
         {id: 5, title: 'Javanese Traditional', desc: 'Baju khas jawa elegan abis', price: 899000, img:Item5},
         {id: 6, title: 'Scadia', desc: 'Google Stadia, you can play with ur phone..cool!', price: 4300000 , img:Item6},
     ],
     addedItems: [],
     total: 0
 }

 const cartReducer= (state = initState,action)=>{
   
    if(action.type === ADD_TO_CART){
          let addedItem = state.items.find(item=> item.id === action.id)
         let existed_item= state.addedItems.find(item=> action.id === item.id)
         if(existed_item)
         {
            addedItem.quantity += 1 
             return{
                ...state,
                 total: state.total + addedItem.price 
                  }
        }
         else{
            addedItem.quantity = 1;
            let newTotal = state.total + addedItem.price 
            
            return{
                ...state,
                addedItems: [...state.addedItems, addedItem],
                total : newTotal
            }
            
        }
    }

     if(action.type === REMOVE_ITEM){
         let itemToRemove = state.addedItems.find(item=> action.id === item.id)
         let new_items = state.addedItems.filter(item=> action.id !== item.id)

         let newTotal = state.total - (itemToRemove.price * itemToRemove.quantity)
         console.log(itemToRemove)
         return {
             ...state,
             addedItems: new_items,
             total: newTotal
         }
     }


     if(action.type === ADD_QUANTITY){
         let addedItem = state.items.find(item=> item.id === action.id)
         addedItem.quantity += 1
         let newTotal = state.total + addedItem.price
         return{
             ...state,
             total: newTotal
         }
     }

     if(action.type === SUB_QUANTITY){
       let addedItem = state.items.find(item => item.id === action.id)

       if(addedItem.quantity === 1){
           let new_items = state.addedItems.filter(item=>item.id !== action.id)
           let newTotal = state.total - addedItem.price
           return {
               ...state,
               addedItems: new_items,
               total: newTotal
           }
       }
       else{
           addedItem.quantity -= 1 
           let newTotal = state.total - addedItem.price
           return {
               ...state,
               total: newTotal
           }
       }
     }

     if(action.type=== ADD_SHIPPING){
        return{
            ...state,
            total: state.total + 6
        }
  }

  if(action.type=== 'SUB_SHIPPING'){
      return{
          ...state,
          total: state.total - 6
      }
}

    else{
        return state
    }
  }

  export default cartReducer